import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    PanelHeader,
    PanelHeaderBack,
    SimpleCell,
    HorizontalCell,
    HorizontalScroll, ScreenSpinner, PromoBanner
} from "@vkontakte/vkui";
import {
    getUrlParams,
    isPlatformDesktop, isPlatformIOS, shortIntegers
} from "../../js/utils";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";
import bridge from "@vkontakte/vk-bridge";
import {Icon20VotestTransferCircleFillTurquoise, Icon28LikeFillRed} from "@vkontakte/icons";

const isDesktop = isPlatformDesktop();

export class Stats extends PureComponent {

    render() {
        const
            {t} = this.props,
            {state, socket} = t,
            {user, banners} = t.state
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && ''
                    }
                </PanelHeader>
                <div className='Stats'>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}></h2>
                    }
                    <p style={{margin: '8px 40px 0 0'}}>
                            ,   .
                    </p>
                    <div className='StatsCards__Container'>
                        <div>
                            <Icon20VotestTransferCircleFillTurquoise width={16} height={16}/>
                            <span>{shortIntegers(user.coins)}</span>
                        </div>
                        <div>
                            <span>{shortIntegers(user.likes)}</span>
                            <Icon28LikeFillRed width={16} height={16}/>
                        </div>
                    </div>
                    <div className='Rating__List' style={{paddingBottom: banners[4] && 140}}>
                        {
                            [
                                ['users_topPictures', ' ', 'pictures'],
                                ['users_topGuessedPictures', ' ', 'guessedPictures'],
                                ['users_topLikes', '', 'likes']
                            ].map((value, index) =>
                                <div>
                                    <span className='Span'>
                                           {value[1]}:
                                    </span>
                                    <HorizontalScroll
                                        showArrows
                                        getScrollToLeft={(i) => i - 64 * 2}
                                        getScrollToRight={(i) => i + 64 * 2}
                                    >
                                        <div style={{display: 'flex'}}>
                                            {
                                                state[value[0]].map((value1, index1) =>
                                                    <HorizontalCell
                                                        key={`Cell_${index1}`}
                                                        header={vk_local_users[value1.id].first_name}
                                                        subtitle={value1[value[2]]}
                                                        onClick={() => {
                                                            t.setPopout(<ScreenSpinner/>);
                                                            socket.call('users.getById', {id: value1.id}, async r => {
                                                                if (r.response !== null) {
                                                                    await t.setState({
                                                                        author: r.response
                                                                    });
                                                                    socket.call('games.getByDrawerId', {id: value1.id}, async r => {
                                                                        await t.setState({
                                                                            author_works: r.response
                                                                        });
                                                                        t.go('author');
                                                                        t.setPopout(null);
                                                                    });
                                                                } else {
                                                                    t.setPopout(null);
                                                                }
                                                            });
                                                        }}
                                                    >
                                                        <Avatar size={48} shadow={false}
                                                                src={vk_local_users[value1.id].photo_100}/>
                                                    </HorizontalCell>
                                                )
                                            }
                                        </div>
                                    </HorizontalScroll>
                                </div>
                            )
                        }
                    </div>
                    {
                        banners[4] &&
                        <PromoBanner
                            bannerData={banners[4]}
                            onClose={async () => {
                                banners[4] = false;
                                await t.setState({banners});
                                this.forceUpdate();
                            }}
                        />
                    }
                </div>
            </React.Fragment>
        )
    }
}

Stats.defaultProps = {};

Stats.propTypes = {
    t: PropTypes.object
};

export default Stats;