import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, Button, ScreenSpinner} from "@vkontakte/vkui";
import {} from "@vkontakte/icons";
import {Icon28Play} from "@vkontakte/icons";
import {decOfNum, isPlatformDesktop, shortIntegers, shortIntegersK} from "../../js/utils";
import {Icon24Like} from "@vkontakte/icons";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";
import {MODAL_CARD_PLAY_MODE, app_version, MODAL_CARD_OUTDATED_VERSION} from "./Drawing";
import {Icon12Chevron} from "@vkontakte/icons";
import {Icon28AddCircleOutline} from "@vkontakte/icons";
import {Icon28ShoppingCartOutline} from "@vkontakte/icons";
import eruda from 'eruda';

const isDesktop = isPlatformDesktop();

export class Main extends PureComponent {

    constructor(props) {
        super(props);

        props.t.socket.unsubscribe('changeOnline');
        props.t.socket.subscribe('changeOnline', r => {
            props.t.setState({online: r.count});
            this.forceUpdate();
        });
    }

    render() {
        const
            {t} = this.props,
            {state, _tags} = t,
            {gamesAsWinner, gamesAsDrawer, top_number, online, user} = state,
            {lvl, pictures, successfulPictures, guessedPictures, likes} = user,
            offset = [
                Math.min(t.state.colors.length - 3, Math.max(lvl, 4) - 4),
                Math.min(t.state.colors.length, Math.max(lvl, 3) + 3)
            ],
            colors = t.state.colors.slice(offset[0], offset[1]),
            maxLvl = Math.min(lvl, t.state.colors.length)
        ;

        if (offset[0] > t.state.colors.length - (isDesktop ? 8 : 6)) {
            offset[0] = t.state.colors.length - (isDesktop ? 8 : 6);
        }

        return (
            <div className='Main'>
                <div
                    className='app_version'
                    onClick={async () => {
                        if (state.app_version !== app_version) {
                            t.setActiveModal(MODAL_CARD_OUTDATED_VERSION);
                        }
                        await t.setState({__v_clicks: state.__v_clicks === 4 ? 4 : (state.__v_clicks + 1)});
                        this.forceUpdate();
                        if (state.__v_clicks === 3) {
                            eruda.init();
                        }
                    }}
                >v{app_version} {state.app_version !== app_version && <span>()</span>}
                </div>
                <div className='User_Header'>
                    <Avatar src={t.state.vk_user.photo_100} size={48} shadow={false}/>
                    <div className='UserTags'>
                        {
                            _tags().map(value =>
                                user[value[0]] && React.cloneElement(value[1], {onClick: () => t.setActiveModal(value[0])})
                            )
                        }
                    </div>
                </div>
                <h3>, {t.state.vk_user.first_name}</h3>
                <h1> </h1>
                <div className='Colors__Container'>
                    <div className='Gradient_Opacity'/>
                    {
                        t.state.colors.map((value, index) =>
                            <div
                                className='Color'
                                key={`Color__${index}`}
                                style={{
                                    display: (offset[0] > index || offset[1] < index) && 'none',
                                    marginLeft: index > 0 && 12,
                                    background: index + 1 === maxLvl && 'linear-gradient(156.04deg, #F05F88 0%, #FBD20A 100%)',
                                    border: index + 1 === maxLvl && 'none'
                                }}
                            >
                                <span>{index + 1}</span>
                                <div
                                    style={{background: typeof value === 'object' ? `conic-gradient(from 180deg at 50% 50%, ${value.join(', ')})` : value}}/>
                            </div>
                        )
                    }
                </div>
                <p>   ,    </p>
                <div className='Cards__Container'>
                    <div onClick={() => {
                        if (gamesAsDrawer.length > 0) {
                            t.go('gallery')
                        }
                    }}>
                        <span
                            className='Top'>{decOfNum(successfulPictures, ['', '', ''])}</span>
                        <div className='Images'>
                            {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                gamesAsDrawer[value] && <img alt='img' src={gamesAsDrawer[value]['Picture.url']}/>
                            }</div>)}
                        </div>
                        <div className='Texts'>
                            <span></span>
                            <span>{decOfNum(pictures, ['', '', ''])}</span>
                        </div>
                    </div>

                    <div onClick={() => t.go('rating')}>
                        <span className='Top'>{top_number && `  ${shortIntegersK(top_number)} `}</span>
                        <div className='Images'>
                            {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                gamesAsWinner[value] && <img alt='img' src={gamesAsWinner[value]['Picture.url']}/>
                            }</div>)}
                        </div>
                        <div className='Texts'>
                            <span></span>
                            <span>{decOfNum(guessedPictures, ['', '', ''])}</span>
                        </div>
                    </div>
                </div>
                <div className='Cards2__Container'>
                    <div onClick={async () => {
                        t.setPopout(<ScreenSpinner/>);
                        t.socket.call('users.getTop', {type: 'pictures'}, r => {
                            t.setState({users_topPictures: r.response});
                            let user_ids = r.response.map(value => value.id);

                            t.socket.call('users.getTop', {type: 'guessed_pictures'}, r => {
                                t.setState({users_topGuessedPictures: r.response});
                                user_ids = [...user_ids, ...r.response.map(value => value.id)];

                                t.socket.call('users.getTop', {type: 'likes'}, async r => {
                                    t.setState({users_topLikes: r.response});
                                    user_ids = [...user_ids, ...r.response.map(value => value.id)];
                                    await getVKUsers(user_ids);

                                    t.setPopout(null);
                                    t.go('stats');
                                });
                            });
                        });
                    }}>
                        <span></span>
                        <Icon12Chevron/>
                    </div>
                    <div onClick={async () => {
                        t.setPopout(<ScreenSpinner/>);
                        t.socket.call('pictures.getRandomLikes', {limit: 10}, r => {
                            t.setState({randomGamesLike: r.response});
                            let user_ids = r.response.map(value => value.drawerId);

                            t.socket.call('pictures.getLikesTop', {limit: 10}, async r => {
                                t.setState({topLikes: r.response});
                                user_ids = [...user_ids, ...r.response.map(value => value.drawerId)];
                                await getVKUsers(user_ids);

                                t.setPopout(null);
                                t.go('likes');
                            });
                        });
                    }}>
                        <span> </span>
                        <Icon12Chevron/>
                    </div>
                </div>
                <div className='CardButton' onClick={async () => {
                    t.go('shop');
                    /*t.setPopout(<ScreenSpinner/>);
                    t.socket.call('shop.getItems', {limit: 10}, r => {

                    });*/
                }}>
                    {isPlatformDesktop() && <Icon28ShoppingCartOutline/>}
                    <span></span>
                    {!isPlatformDesktop() && <Icon12Chevron/>}
                </div>
                {
                    !isPlatformDesktop() && <div style={{height: 111, width: 1}}/>
                }
                <div className='PlayContainer'>
                    <div>
                        <div className='ButtonPlayGlobal' onClick={() => t.go('search')}>
                            <Icon28Play/>
                        </div>
                        <div className='ButtonPlayFriends' onClick={() => t.go('friends')}>
                            <Icon28AddCircleOutline/>
                        </div>
                    </div>
                    <p>: {online}</p>
                </div>
                <div className='MainTint'/>
            </div>
        )
    }
}

Main.defaultProps = {};

Main.propTypes = {
    t: PropTypes.object
};

export default Main;