import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    PanelHeader,
    PanelHeaderBack, PromoBanner,
    SimpleCell,
} from "@vkontakte/vkui";
import {
    getUrlParams,
    isPlatformDesktop, isPlatformIOS
} from "../../js/utils";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";
import bridge from "@vkontakte/vk-bridge";

const isDesktop = isPlatformDesktop();

export class Friends extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            friends: [],
            others: []
        };
    }

    async updateFriends() {
        let friends = [];
        if (!this.props.t.state.isNotApp) {
            const
                access_token = (await bridge.send('VKWebAppGetAuthToken', {
                    app_id: parseInt(getUrlParams().vk_app_id),
                    scope: 'friends'
                })).access_token,
                _friends = await bridge.send('VKWebAppCallAPIMethod', {
                    method: 'friends.get',
                    params: {access_token, v: '5.131'}
                }),
                friends_count = _friends.response.count,
                j = Math.floor(friends_count / 100)
            ;

            friends = [..._friends.response.items];

            for (let i = 0; i < j; i++) {
                friends = friends.concat((await bridge.send('VKWebAppCallAPIMethod', {
                    method: 'friends.get',
                    params: {access_token, v: '5.131', offset: i * 100}
                })).response.items);
            }

            await getVKUsers(friends);
        }
        const {socket} = this.props.t;
        socket.call('friends.getLobbies', {ids: friends}, async r => {
            const friend_ids = r.response;
            await getVKUsers(r.public_lobbies);
            this.setState({friends: friend_ids, others: r.public_lobbies});
        });
    }

    componentDidMount() {
        const {socket} = this.props.t;
        socket.unsubscribe('createLobby', 'removeLobby');
        socket.subscribe('createLobby', async r => {
            const {friends, others} = this.state;
            if (r.changeMode && others.indexOf(r.id) === -1 && friends.indexOf(r.id) === -1) {
                await getVKUsers([r.id]);
                this.setState({others: [...others, r.id]});
            } else if (!r.changeMode && friends.indexOf(r.id) === -1) {
                this.setState({friends: [...friends, r.id]});
            }
        });
        socket.subscribe('removeLobby', async r => {
            const {friends, others} = this.state;
            if (r.changeMode && others.indexOf(r.id) > -1 && friends.indexOf(r.id) === -1) {
                others.splice(others.indexOf(r.id), 1);
                this.setState({others});
            } else if (!r.changeMode && friends.indexOf(r.id) > -1) {
                friends.splice(friends.indexOf(r.id), 1);
                this.setState({friends});
            }
            this.forceUpdate();
        });

        this.updateFriends();
    }

    render() {
        const
            {t} = this.props,
            {socket} = t,
            {user, banners} = t.state,
            {friends, others} = this.state
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && ' '
                    }
                </PanelHeader>
                <div className='Friends'>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}> </h2>
                    }
                    <p style={{margin: '8px 40px 0 0'}}>
                             .
                    </p>
                    <p style={{margin: '4px 40px 0 0'}}>
                               20.
                    </p>
                    <div style={{display: 'flex', marginTop: 24}}>
                        <Button
                            stretched
                            size='m'
                            mode='gradient_violet'
                            onClick={() => {
                                if (user.vk_donut || user.admin) {
                                    socket.call('friends.createLobby', {}, r => {
                                        if (r.response) {
                                            t.setState({friend_lobby: r.response});
                                            t.go('friend_lobby');
                                        }
                                    });
                                } else {
                                    if (isPlatformIOS()) {
                                        t.go('vk_donut_ios');
                                    } else {
                                        t.go('vk_donut');
                                    }
                                }
                            }}
                        >
                             
                        </Button>
                        <Button
                            style={{marginLeft: 4}}
                            stretched
                            size='m'
                            mode='gradient_gray'
                            onClick={async () => {
                                await this.updateFriends();
                            }}
                        >
                             
                        </Button>
                    </div>
                    <div className='FriendsList' style={{paddingBottom: banners[7] && 140}}>
                        {
                            friends.length > 0 ?
                                <React.Fragment>
                                    <span className='Span'>
                                         ,     :
                                    </span>
                                    <div style={{marginTop: 8}}>
                                        {
                                            friends.map((value, index) =>
                                                <SimpleCell
                                                    key={`SCell_${index}`}
                                                    before={<Avatar size={32} shadow={false}
                                                                    src={vk_local_users[value].photo_100}/>}
                                                    expandable={true}
                                                    onClick={() => {
                                                        socket.call('friends.joinLobby', {owner_id: value}, async r => {
                                                            await getVKUsers(r.response.members);
                                                            await t.setState({friend_lobby: r.response});
                                                            t.go('friend_lobby');
                                                        });
                                                    }}
                                                >
                                                    {vk_local_users[value].first_name} {vk_local_users[value].last_name.substring(0, 1)}.
                                                </SimpleCell>
                                            )
                                        }
                                    </div>
                                </React.Fragment>
                                :
                                <span className='Span'>
                                          :(
                                </span>
                        }
                        {
                            others.length > 0 &&
                            <React.Fragment>
                                <br/>
                                <div className='Span' style={{marginTop: 12}}>
                                     :
                                </div>
                                <div style={{marginTop: 8}}>
                                    {
                                        others.map((value, index) =>
                                            <SimpleCell
                                                key={`SCell_${index}`}
                                                before={<Avatar size={32} shadow={false}
                                                                src={vk_local_users[value].photo_100}/>}
                                                expandable={true}
                                                onClick={() => {
                                                    socket.call('friends.joinLobby', {owner_id: value}, async r => {
                                                        await getVKUsers(r.response.members);
                                                        await t.setState({friend_lobby: r.response});
                                                        t.go('friend_lobby');
                                                    });
                                                }}
                                            >
                                                {vk_local_users[value].first_name} {vk_local_users[value].last_name.substring(0, 1)}.
                                            </SimpleCell>
                                        )
                                    }
                                </div>
                            </React.Fragment>
                        }
                    </div>
                    {
                        banners[7] &&
                        <PromoBanner
                            bannerData={banners[7]}
                            onClose={async () => {
                                banners[7] = false;
                                await t.setState({banners});
                                this.forceUpdate();
                            }}
                        />
                    }
                </div>
            </React.Fragment>
        )
    }
}

Friends.defaultProps = {};

Friends.propTypes = {
    t: PropTypes.object
};

export default Friends;