import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, Button, IconButton, Input, ScreenSpinner, Snackbar, WriteBarIcon} from "@vkontakte/vkui";
import {Icon24Like, Icon28ChevronRightOutline, Icon28ReportOutline, Icon28ShareOutline} from "@vkontakte/icons";
import bridge from "@vkontakte/vk-bridge";
import {adAppApi, decOfNum, getSrcUrl, getUrlParams} from "../../js/utils";
import {vk_local_users, getVKUsers} from "../../js/drawerapp/utils";

export class Result extends PureComponent {
    constructor(props) {
        super(props);

        const {drawerId, winnerId, word, t} = props;

        this.state = {
            isDrawer: drawerId === t.state.vk_user.id,
            isWinner: winnerId === t.state.vk_user.id,
            winnerId: winnerId,
            drawerId: drawerId,
            word
        };

        this.shareStory = this.shareStory.bind(this);
    }

    componentDidMount() {

    }

    shareStory() {
        this.props.t.setPopout(<ScreenSpinner/>);
        const
            {createCanvas, loadImage} = require('canvas'),
            {isDrawer, winnerId, isWinner} = this.state,
            type = isDrawer ? 1 : isWinner ? 2 : 3
        ;

        loadImage(getSrcUrl(require(`../../assets/drawing/stories/Story${type}.png`))).then(async background => {
            const
                canvas = createCanvas(background.width, background.height),
                ctx = canvas.getContext('2d'),
                img = new Image(),
                {word} = this.state,
                title = word.nom.substring(0, 1).toUpperCase() + word.nom.substring(1)
            ;
            ctx.drawImage(background, 0, 0);

            if (type === 1) {
                ctx.font = '64px TT Commons Demibold';
                ctx.textAlign = 'center';
                ctx.fillStyle = '#616161';
                ctx.fillText(title, 539, 708 + 13 + 489);
            }

            img.src = this.props.t.state.canvas_;
            ctx.drawImage(img, 198, 618, 683, 683);

            try {
                await bridge.send('VKWebAppShowStoryBox', {
                    background_type: 'image',
                    blob: canvas.toDataURL('image/png'),
                    attachment: {
                        url: 'https://vk.com/app' + getUrlParams().vk_app_id,
                        text: 'go_to',
                        type: 'url'
                    }
                });
                this.props.t.socket.call('games.share', {gameId: this.props.gameId});
                adAppApi('stats.shareStory');
            } catch (e) {
            }
            this.props.t.updateData();
            this.props.t.setPopout(null);
            this.props.t.setActivePanel('main');
        });
    }

    render() {
        const
            {t, gameId} = this.props,
            {vk_user, isNotApp} = t.state,
            {isDrawer, isWinner, winnerId, drawerId, word, isLiked} = this.state,
            time = Math.round((t.state.gameEnd - t.state.gameStart) / 1000)
        ;

        return (
            <div className='Result'>
                {
                    isDrawer ?
                        winnerId !== 0 ?
                            <React.Fragment>
                                <h1> !</h1>
                                <p>    {decOfNum(time || 0, ['', '', ''])}</p>
                            </React.Fragment>
                            :
                            <React.Fragment>
                                <h1> </h1>
                                <p>  ,     </p>
                            </React.Fragment>
                        :
                        winnerId !== 0 ?
                            isWinner ?
                                <React.Fragment>
                                    <h1> !</h1>
                                    <p> {vk_user.sex === 1 ? '' : ''} {vk_user.sex === 1 && ''},
                                         {vk_local_users[drawerId].first_name} {vk_local_users[drawerId].sex === 1 && ''} 
                                        </p>
                                </React.Fragment>
                                :
                                <React.Fragment>
                                    <h1> </h1>
                                    {
                                        vk_local_users[winnerId] &&
                                        <p>
                                            {vk_local_users[winnerId].first_name} {vk_local_users[drawerId].sex === 1 && ''} ,
                                             ,    
                                        </p>
                                    }
                                </React.Fragment>
                            :
                            <React.Fragment>
                                <h1> </h1>
                                <p>  ,    </p>
                            </React.Fragment>
                }
                <div className='Canvas_Container'>
                    {
                        !isDrawer &&
                        <Icon28ReportOutline className='Report' onClick={() => {
                            t.setPopout(<ScreenSpinner/>);
                            t.socket.call('games.report', {game_id: gameId}, r => {
                                if (r.response) {
                                    t.setState({
                                        snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                             
                                        </Snackbar>
                                    });
                                } else {
                                    t.setState({
                                        snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                              
                                        </Snackbar>
                                    });
                                }
                                t.setPopout(null);
                            });
                        }}/>
                    }
                    <Icon24Like fill={isLiked ? '#F16384' : 'var(--color_secondary)'} className='Like' onClick={() => {
                        t.setPopout(<ScreenSpinner/>);
                        t.socket.call('pictures.like', {game_id: gameId}, r => {
                            this.setState({isLiked: r.response.isLiked});
                            t.setPopout(null);
                        });
                    }}/>
                    {
                        (word && word.nom) &&
                        <h2>{word.nom.substring(0, 1).toUpperCase() + word.nom.substring(1)}</h2>
                    }
                    <img alt='canvas' src={t.state.canvas_}/>
                    <Button
                        before={<Icon28ShareOutline width={16} height={16}/>}
                        size='m' mode='gradient'
                        onClick={() => {
                            this.shareStory();
                        }}
                    >
                        
                    </Button>
                </div>
                <Button
                    size='m' mode='secondary'
                    onClick={async () => {
                        const
                            {socket, state} = this.props.t,
                            {subGameInfo} = state
                        ;
                        t.setPopout(<ScreenSpinner/>);
                        try {
                            if (!isNotApp) await bridge.send('VKWebAppShowNativeAds', {ad_format: 'interstitial'});
                        } catch (e) {
                        }
                        t.setPopout(null);
                        t.updateData();
                        if (subGameInfo.friendly) {
                            if (subGameInfo.owner_id === t.state.user.id) {
                                socket.call('friends.createLobby');
                                await t.setState({friend_lobby: {owner_id: t.state.user.id, members: []}});
                                t.setActivePanel('friend_lobby', ['main', 'friends']);
                            } else {
                                socket.call('friends.joinLobby', {owner_id: subGameInfo.owner_id}, async r => {
                                    if (r.response) {
                                        await getVKUsers(r.response.members);
                                        await t.setState({friend_lobby: r.response});
                                        t.setActivePanel('friend_lobby', ['main', 'friends']);
                                    } else {
                                        t.setActivePanel('friends', ['main']);
                                    }
                                });
                            }
                        } else {
                            t.setActivePanel('main');
                        }
                    }}
                >
                     
                </Button>
            </div>
        )
    }
}

Result.defaultProps = {};

Result.propTypes = {
    t: PropTypes.object,
    drawerId: PropTypes.number,
    winnerId: PropTypes.number,
    word: PropTypes.object,
    gameId: PropTypes.number
};

export default Result;