import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    ActionSheet,
    ActionSheetItem,
    Avatar,
    Button,
    IconButton,
    Input,
    ScreenSpinner,
    Snackbar,
    WriteBarIcon
} from "@vkontakte/vkui";
import {getBase64Image, isPlatformDesktop} from "../../js/utils";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";
import {ReactComponent as IconErase} from "../../assets/drawing/icons/IconErase.svg";
import {Icon16Cancel, Icon16Done, Icon20Cancel} from "@vkontakte/icons";

const current = {};
let interval, lastColorIndex = 0;

export class Game extends PureComponent {
    constructor(props) {
        super(props);

        const {user} = props.t.state;
        this.state = {
            drawer: vk_local_users[props.drawerId],
            isDrawer: props.online === false ? true : (props.drawerId === props.t.state.vk_user.id),
            text: '',
            selectedColor: (user.vk_donut || user.admin) ? 0 : 1,
            drawing: false,
            time: 90,

            messages: [],

            isEraser: false
        };
        if (!this.state.drawer) {
            setTimeout(async () => {
                await getVKUsers([props.drawerId]);
                this.setState({drawer: vk_local_users[props.drawerId]})
            }, 1);
        }

        this.onMouseDown = this.onMouseDown.bind(this);
        this.onMouseUp = this.onMouseUp.bind(this);
        this.throttle = this.throttle.bind(this);
        this.onMouseMove = this.onMouseMove.bind(this);
        this.drawLine = this.drawLine.bind(this);

        if (props.online) {
            interval = setInterval(() => {
                this.setState({time: this.state.time - 1});
                if (this.state.time <= -5) {
                    this.props.t.setState({
                        canvas_: this.canvas.toDataURL('image/png'),
                        winnerId: 0,
                        word: {nom: ''},
                        gameId: 0,
                        gameEnd: Date.now()
                    });
                    this.props.t.setActivePanel('result');
                    console.log('Game end (timeout)');
                    clearInterval(interval);
                }
            }, 1000);

            props.t.socket.subscribe('gameEnd', async r => {
                const
                    canvasData = this.canvas.toDataURL('image/png'),
                    {winnerId, word, gameId} = r
                ;
                if (this.state.isDrawer) {
                    this.props.t.socket.call('games.end', {canvasData, lobbyId: this.props.lobbyId, gameId});
                }
                this.props.t.setState({canvas_: canvasData, winnerId, word, gameId, gameEnd: Date.now()});
                this.props.t.setActivePanel('result');
                console.log('Game end');
            });

            if (!this.state.isDrawer) {
                props.t.socket.subscribe('draw', r => {
                    const {x0, y0, x1, y1, color, isEraser} = r.data;
                    this.drawLine(x0, y0, x1, y1, color, isEraser);
                });

                props.t.socket.subscribe('clear', r => {
                    this.canvasContext.clearRect(0, 0, 319, 319);
                });
            }

            props.t.socket.subscribe('message', async r => {
                const {user_id, message} = r;
                console.log(`New message from ${user_id}: ${message}`);
                await this.setState({
                    messages: [...this.state.messages, {
                        user_id,
                        message, ...vk_local_users[user_id]
                    }]
                });

                try {
                    const comments = document.getElementsByClassName('Comment');
                    comments[comments.length - 1].scrollIntoView(false);
                } catch (e) {

                }
            });
        }
    }

    componentWillUnmount() {
        if (this.props.online) {
            clearInterval(interval);
        }
    }

    async componentDidMount() {
        const
            {canvas, onMouseDown, onMouseUp, throttle, onMouseMove, props} = this,
            {drawer, isDrawer} = this.state,
            {online, t} = props
        ;
        this.canvasContext = canvas.getContext('2d');

        if (isDrawer) {
            canvas.addEventListener('mousedown', onMouseDown, false);
            canvas.addEventListener('mouseup', onMouseUp, false);
            canvas.addEventListener('mouseout', onMouseUp, false);
            canvas.addEventListener('mousemove', throttle(onMouseMove, 10), false);

            //Touch support for mobile devices
            canvas.addEventListener('touchstart', onMouseDown, false);
            canvas.addEventListener('touchend', onMouseUp, false);
            canvas.addEventListener('touchcancel', onMouseUp, false);
            canvas.addEventListener('touchmove', throttle(onMouseMove, 10), false);
        }

        if (!online) {
            t.setPopout(<ScreenSpinner/>);
            const
                picUrl = t.state.pictureUrl_,
                canvasData = await getBase64Image(picUrl),
                {loadImage} = require('canvas')
            ;
            this.canvasContext.drawImage(await loadImage(canvasData), 0, 0);
            t.setPopout(null);
        }
    }

    onMouseDown(e) {
        this.setState({drawing: true});
        current.x = e.clientX || e.touches[0].clientX;
        current.y = e.clientY || e.touches[0].clientY;
    }

    onMouseUp(e) {
        const {drawing, selectedColor, isEraser} = this.state;
        if (!drawing) {
            return;
        }
        this.setState({drawing: false});
        try {
            const
                {canvas} = this,
                canvasRect = canvas.getBoundingClientRect()
            ;
            this.drawLine(current.x - canvasRect.left, current.y - canvasRect.top, (e.clientX || e.touches[0].clientX) - canvasRect.left, (e.clientY || e.touches[0].clientY) - canvasRect.top, this.props.t.state.colors[selectedColor], isEraser);
        } catch (er) {
        }
    }

    onMouseMove(e) {
        const {drawing, selectedColor, isEraser} = this.state;
        if (!drawing) {
            return;
        }
        const
            {canvas} = this,
            canvasRect = canvas.getBoundingClientRect()
        ;
        this.drawLine(current.x - canvasRect.left, current.y - canvasRect.top, (e.clientX || e.touches[0].clientX) - canvasRect.left, (e.clientY || e.touches[0].clientY) - canvasRect.top, this.props.t.state.colors[selectedColor], isEraser);
        current.x = e.clientX || e.touches[0].clientX;
        current.y = e.clientY || e.touches[0].clientY;
        return false;
    }

    drawLine(x0, y0, x1, y1, color, isEraser) {
        try {
            const
                {lobbyId, t} = this.props,
                {canvas, canvasContext} = this
            ;

            if (typeof color === 'object') {
                color = color[lastColorIndex];
                lastColorIndex += (color.length - 1) === lastColorIndex ? -lastColorIndex : 1;
            }

            canvasContext.beginPath();

            if (isEraser) {
                canvasContext.globalCompositeOperation = 'destination-out';
                canvasContext.arc(x0, y0, 8, 0, Math.PI * 2, false);
                canvasContext.fill();
            } else {
                canvasContext.globalCompositeOperation = 'source-over';
                canvasContext.moveTo(x0, y0);
                canvasContext.lineTo(x1, y1);
                canvasContext.strokeStyle = color;
                canvasContext.lineWidth = 2;
                canvasContext.stroke();
            }

            canvasContext.closePath();

            if ((this.state.isDrawer && this.props.online) || t.state.user.admin) {
                this.props.t.socket.call('games.draw', {
                    lobbyId,
                    data: {
                        x0,
                        y0,
                        x1,
                        y1,
                        color,
                        isEraser
                    }
                });
            }
        } catch (e) {
        }
    }

    throttle(callback, delay) {
        var previousCall = new Date().getTime();
        return function () {
            const time = new Date().getTime();

            if ((time - previousCall) >= delay) {
                previousCall = time;
                callback.apply(null, arguments);
            }
        };
    }

    render() {
        const
            {t, lobbyId, online, word} = this.props,
            {colors, user} = t.state,
            {drawer, isDrawer, text, selectedColor, time, messages, isEraser} = this.state,
            isDesktop = isPlatformDesktop()
        ;

        return (
            <div className='Game'>
                <div style={{width: isDesktop ? 319 : '100%', alignSelf: 'flex-start'}} ref={ref => this.header = ref}>
                    <div style={{display: 'flex', justifyContent: 'space-between'}}>
                        <h2 style={{textAlign: 'left'}}>
                            {isDrawer ? ': ' + word.nom : ': ' + drawer.first_name}
                        </h2>
                        {
                            online && <h2 style={{
                                textAlign: 'right',
                                width: '25%',
                                color: 'var(--color_secondary)',
                                wordWrap: 'normal'
                            }}>
                                {time >= 0 ? time : 0} 
                            </h2>
                        }
                    </div>
                </div>
                <div style={{display: (isDesktop && online) && 'flex'}}>
                    <div className='Canvas_Container'>
                        <IconButton
                            style={{display: !isDrawer && 'none'}}
                            className='Icon__Clear'
                            hasHover={false}
                            onClick={() => {
                                if (online) {
                                    t.socket.call('games.clear', {lobbyId});
                                }
                                this.canvasContext.clearRect(0, 0, 319, 319);
                            }}
                        >
                            <Icon20Cancel width={21} height={21} fill='#C3C3C3'/>
                        </IconButton>
                        <canvas width={319} height={319} ref={ref => this.canvas = ref} id='canvas'/>
                        <div style={{display: !isDrawer && 'none'}}>
                            <div>
                                {
                                    colors.map((value, index) =>
                                        <div
                                            onClick={() => {
                                                if (typeof value === 'object' && !(user.vk_donut || user.admin))
                                                    return;

                                                this.setState({selectedColor: index, isEraser: false})
                                            }}
                                            className='Color'
                                            key={`Color__${index}`}
                                            style={{
                                                marginLeft: index > 0 && 8,
                                                background: typeof value === 'object' ? `conic-gradient(from 180deg at 50% 50%, ${value.join(', ')})` : value,
                                                outline: selectedColor === index && '4px solid #F3F2F2',
                                                opacity: user.lvl < (index + 1) && .25,
                                                pointerEvents: user.lvl < (index + 1) && 'none'
                                            }}
                                        />
                                    )
                                }
                            </div>
                            <div>
                                <IconButton
                                    style={{
                                        height: 'auto',
                                        margin: 12,
                                        padding: 4,
                                        background: isEraser && '#F3F2F2',
                                        display: 'flex'
                                    }}
                                    hasHover={false} onClick={() => {
                                    this.setState({isEraser: true, selectedColor: -1});
                                }}>
                                    <IconErase/>
                                </IconButton>
                            </div>
                        </div>
                    </div>
                    {
                        !online &&
                        <div className='Offline_Drawing__Buttons'>
                            <Button
                                before={<Icon16Cancel fill='var(--color_secondary)'/>}
                                size='m' mode='secondary'
                                onClick={() => {
                                    t.back();
                                }}
                            >
                                
                            </Button>
                            <Button
                                style={{marginLeft: 10}}
                                before={<Icon16Done fill='var(--color_secondary)'/>}
                                size='m' mode='secondary'
                                onClick={() => {
                                    t.setPopout(<ScreenSpinner/>);
                                    const
                                        canvasData = this.canvas.toDataURL('image/png'),
                                        {gameId_} = t.state
                                    ;
                                    if (this.state.isDrawer) {
                                        this.props.t.socket.call('games.savePicture', {
                                            canvasData, gameId: gameId_
                                        }, async r => {
                                            if (r.response) {
                                                t.setState({
                                                    n_canvasData: canvasData,
                                                    snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                         
                                                    </Snackbar>
                                                });
                                                await new Promise(resolve =>
                                                    this.props.t.socket.call('games.getByDrawerId', {
                                                        id: this.props.t.state.vk_user.id,
                                                        limit: this.props.t.state.gamesAsDrawer.length
                                                    }, async r => {
                                                        await this.props.t.setState({gamesAsDrawer: r.response});
                                                        resolve(true);
                                                    })
                                                );
                                                t.forceUpdate();
                                            } else {
                                                t.setState({
                                                    snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                          
                                                    </Snackbar>
                                                });
                                            }
                                            setTimeout(() => {
                                                t.setPopout(null);
                                                t.back();
                                                t.forceUpdate();
                                            }, 1000);
                                        });
                                    }
                                }}
                            >
                                
                            </Button>
                        </div>
                    }
                    {
                        online &&
                        <div className='Comments_Container'>
                            <div className='Comments' ref={ref => this.comments = ref}>
                                {
                                    messages.map((value, index) =>
                                        <div
                                            className='Comment' key={`div__${index}`} style={{
                                            marginTop: index > 0 && 6,
                                            cursor: value.id === t.state.vk_user.id && 'default'
                                        }}
                                            onClick={() => {
                                                if (value.id !== t.state.vk_user.id && value.message.replaceAll('*', '').length > 0) {
                                                    t.setPopout(<ActionSheet
                                                        onClose={() => t.setPopout(null)}
                                                        iosCloseItem={<ActionSheetItem
                                                            autoclose mode='cancel'>
                                                            
                                                        </ActionSheetItem>}
                                                    >
                                                        <ActionSheetItem
                                                            autoclose mode='destructive'
                                                            onClick={() => {
                                                                t.socket.call('games.reportWord', {message: value.message});
                                                                t.setState({
                                                                    snackbar: <Snackbar
                                                                        onClose={() => t.setState({snackbar: null})}>
                                                                         
                                                                    </Snackbar>
                                                                });
                                                            }}
                                                        >
                                                            
                                                        </ActionSheetItem>
                                                    </ActionSheet>)
                                                }
                                            }}
                                        >
                                            <Avatar size={32} shadow={false} src={value.photo_100}/>
                                            <div>
                                                <div>{value.first_name} {value.last_name}</div>
                                                <div>{value.message}</div>
                                            </div>
                                        </div>
                                    )
                                }
                            </div>
                            {
                                !isDrawer &&
                                <div className='Comment_Input'>
                                    <Input
                                        autoFocus={true}
                                        id='inputComment'
                                        value={text}
                                        onChange={(e) => {
                                            if (e.currentTarget.value.length <= 120) {
                                                this.setState({text: e.currentTarget.value});
                                            }
                                        }}
                                        placeholder=' ?'
                                        onKeyDown={(e) => {
                                            if (e.key === 'Enter' && text.trim().length > 0) {
                                                console.log(`Send message (key) (lobbyId = ${lobbyId}): ${text}`);
                                                t.socket.call('games.message', {lobbyId, message: text});
                                                this.setState({text: ''});
                                            }
                                        }}
                                        onFocus={() => {
                                            setTimeout(() => {
                                                document.getElementById('inputComment').scrollIntoView(false);
                                            }, 100);
                                        }}
                                    />
                                    <WriteBarIcon
                                        style={{display: text.length === 0 && 'none'}} mode='send'
                                        onClick={() => {
                                            if (text.trim().length > 0) {
                                                console.log(`Send message (btn) (lobbyId = ${lobbyId}): ${text}`);
                                                t.socket.call('games.message', {lobbyId, message: text});
                                            } else {
                                                console.log(`Message is empty: ${text}`);
                                            }
                                            this.setState({text: ''});
                                        }}
                                    />
                                </div>
                            }
                        </div>
                    }
                </div>
            </div>
        )
    }
}

Game.defaultProps = {};

Game.propTypes = {
    t: PropTypes.object,
    drawerId: PropTypes.number.isRequired,
    lobbyId: PropTypes.number.isRequired,
    online: PropTypes.bool,
    word: PropTypes.object
};

export default Game;